import React from 'react';
import { Link } from 'react-router-dom';
import { MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle } from 'mdbreact';
const Card = (props) => {
	return (
		<MDBCard
			style={{ maxWidth: '20rem' }}
			className='card heavy-rain-gradient responsive text-black m-2 example hoverable animated pulse'
		>
			<MDBCardImage
				className='card-img-top img-fluid'
				src={props.Source}
				waves
			/>
			<MDBCardBody className='text-center card-body'>
				<MDBCardTitle>{props.name}</MDBCardTitle>
				<Link to={props.urls}>
					<button className='btn responsive btn-sm text-light bg-primary rounded-pill'>
						Manage
					</button>
				</Link>
			</MDBCardBody>
		</MDBCard>
	);
};

export default Card;
