import React, { PureComponent } from "react";
import axios from "axios";

export default class Auth2 extends PureComponent {
  state = { Children: null };
  checkAuth = async () => {
    await axios
      .get("/isauth")
      .then(res => {
        this.setState({ Children: this.props.children });
      })
      .catch(err => {
        this.props.history.push("/adminlogin");
        console.log(err);
      });
  };

  componentDidMount() {
    this.checkAuth();
  }
  componentWillUpdate() {
    this.checkAuth();
  }

  render() {
    return <React.Fragment>{this.state.Children}</React.Fragment>;
  }
}
