import React, { Component } from "react";
import { MDBModalHeader } from "mdbreact";
import { Pie } from "react-chartjs-2";

export default class ResultsChart extends Component {
  state = {
    dataPie1: {
      labels: [
        "Course Material Is Provided",
        "In Class Activities",
        "Feedback Help In Learning",
        "Course Material Is Usefull",
        "Has Increased Intrust",
        "Class Rooms Are fine"
      ],
      datasets: [
        {
          data: [],
          backgroundColor: [
            "#F7464A",
            "#46BFBD",
            "#FDB45C",
            "#949FB1",
            "#4D5360",
            "#ac64ad"
          ],
          hoverBackgroundColor: [
            "#FF5A5E",
            "#5AD3D1",
            "#FFC870",
            "#A8B3C5",
            "#616774",
            "#da92db"
          ]
        }
      ]
    },
    dataPie2: {
      labels: [
        "EncouragesStudents",
        "ExplainsWell",
        "WellPreparedLecture",
        "FollowsTimeTable",
        "FairGrading",
        "FriendlyAttitude",
        "Compleated Course",
        "arivers & leaves on time"
      ],
      datasets: [
        {
          data: [],
          backgroundColor: [
            "#F7464A",
            "#46BFBD",
            "#FDB45C",
            "#949FB1",
            "#4D5360",
            "#ac64ad",
            "#FDB45C",
            "#949FB1"
          ],
          hoverBackgroundColor: [
            "#FF5A5E",
            "#5AD3D1",
            "#FFC870",
            "#A8B3C5",
            "#616774",
            "#da92db",
            "#A8B3C5",
            "#5AD3D1"
          ]
        }
      ]
    }
  };
  componentDidMount() {
    if (this.props.iscourse) {
      let n = this.props.piedata.total * 5;
      let data = this.props.piedata.course.map(x => {
        let y = x.response / n;
        return Math.round(y * 100);
      });
      console.log(data);
      this.setState({
        dataPie1: {
          datasets: [
            {
              data: data
            }
          ]
        }
      });
    } else {
      let n = this.props.piedata.total * 5;
      let data = this.props.piedata.teacher.map(x => {
        let y = x.response / n;
        return Math.round(y * 100);
      });
      console.log(data);
      this.setState({
        dataPie2: {
          datasets: [
            {
              data: data
            }
          ]
        }
      });
    }
  }
  render() {
    return (
      <React.Fragment>
        <MDBModalHeader toggle={this.props.toggle}>
          {this.props.iscourse ? (
            <>Course Evaluation Results Details</>
          ) : (
            <>Teacher Evaluation Results Details</>
          )}
        </MDBModalHeader>
        <Pie
          data={this.props.iscourse ? this.state.dataPie1 : this.state.dataPie2}
          height={300}
          options={{ responsive: true }}
        />
      </React.Fragment>
    );
  }
}
