import React, { PureComponent } from "react";
import {
  MDBContainer,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBModalFooter,
  MDBIcon,
  MDBCardHeader,
  MDBBtn,
  MDBInput
} from "mdbreact";
import { NotificationManager } from "react-notifications";
import Navbarr from "../../Containers/User/Navbarr";
import axios from "axios";
class Adminlogin extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: ""
    };
  }

  onUserChange = e => {
    this.setState({
      email: e.target.value
    });
  };
  onPassChange = e => {
    this.setState({
      password: e.target.value
    });
  };

  onSubmit = async e => {
    e.preventDefault();
    let data = { ...this.state };

    await axios
      .post("/adminsignin", data)
      .then(res => {
        NotificationManager.success("Signed IN", "Successful!", 3000);
        this.props.history.push("/AdminPanel");
      })
      .catch(res => {
        NotificationManager.error("Wrong Crdentionals", "Error!", 3000);
        console.log(res);
      });
    this.setState({
      email: "",
      password: ""
    });
  };
  createadminlogin = async () => {
    await axios
      .post("/adminsignup", {
        username: "admin",
        email: "admin@admin.com",
        password: "admin"
      })
      .then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
  };
  componentDidMount() {
    this.createadminlogin();
  }
  render() {
    return (
      <Navbarr {...this.props} check={true} title="Admin Login">
        <MDBContainer
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center"
          }}
          className="mt-5"
        >
          <MDBCol md="4" className="mt-5">
            <MDBCard className="hoverable mt-5 heavy-rain-gradient ">
              <MDBCardBody>
                <MDBCardHeader className="form-header deep-blue-gradient rounded card-cascade-wider card-cascade">
                  <h3 className="my-2">
                    <MDBIcon icon="lock " /> Login:
                  </h3>
                </MDBCardHeader>
                <form onSubmit={this.onSubmit}>
                  <div className="grey-text">
                    <MDBInput
                      label="Type your email"
                      icon="envelope"
                      type="email"
                      group
                      validate
                      error="wrong"
                      success="right"
                      value={this.state.email}
                      onChange={this.onUserChange}
                      required
                    />
                    <MDBInput
                      label="Type your password"
                      icon="lock"
                      group
                      type="password"
                      validate
                      value={this.state.password}
                      onChange={this.onPassChange}
                      required
                    />
                  </div>

                  <div className="text-center mt-4">
                    <MDBBtn
                      color="light-blue"
                      className="mb-3 rounded-pill"
                      type="submit"
                    >
                      Login
                    </MDBBtn>
                  </div>
                </form>
                <MDBModalFooter>
                  <div className="font-weight-light">
                    <p>Forgot Password?</p>
                  </div>
                </MDBModalFooter>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBContainer>
      </Navbarr>
    );
  }
}
export default Adminlogin;
