import ReactDOM from "react-dom";
import * as serviceWorker from "./serviceWorker";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "./App.scss";
import "./index.css";
import React, { Component, PureComponent } from "react";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import Adminlogin from "./Components/Admin/Adminlogin";
import Courses from "./Components/Admin/Courses";
import Questions from "./Components/Admin/Questions";
import AdminPanel from "./Components/Admin/AdminPanel";
import Teachers from "./Components/Admin/Teachers";
import Departments from "./Components/Admin/Departments";
import Results from "./Components/Admin/Results";
import About from "./Components/User/About";
import Home from "./Components/User/Home";
import StudentLogin from "./Components/User/StudentLogin";
import NotFoundPage from "./NotFoundPage";
import StudentPanel from "./Components/User/StudentPanel";
import { NotificationContainer } from "react-notifications";
import "react-notifications/lib/notifications.css";
import Tokens from "./Components/Admin/Tokens";
import Setting from "./Components/Admin/Setting";
import axios from "axios";
import HttpsRedirect from "react-https-redirect";
import Auth from "./HOC/Auth";
axios.defaults.withCredentials = true;
axios.defaults.baseURL = `users/`;
axios.defaults.headers.common["Authorization"] = "AUTH TOKEN";
axios.defaults.headers.post["Content-Type"] = "application/json";

class Routes extends PureComponent {
  state = {
    isauthadmin: false,
    isauthuser: false,
    refs: null
  };
  checkauth = async () => {
    await axios
      .get("/isauth")
      .then(res => {
        this.setState({ isauthadmin: true });
      })
      .catch(err => {
        console.log(err);
      });
    await axios
      .get("/isauthuser")
      .then(res => {
        this.setState({ isauthuser: true });
      })
      .catch(err => {
        console.log(err);
      });
  };
  componentDidMount() {
    this.checkauth();
  }
  render() {
    let rout = (
      <Router>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/Adminlogin" component={Adminlogin} />
          <Route path="/About" component={About} />
          <Route path="/Studentlogin" component={StudentLogin} />
          <Route component={NotFoundPage} />
        </Switch>
        <NotificationContainer />
      </Router>
    );
    if (this.state.isauthadmin) {
      rout = (
        <Router>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/Adminlogin" component={Adminlogin} />
            <Route path="/Courses" component={Courses} />
            <Route path="/Questions" component={Questions} />
            <Route path="/Tokens" component={Tokens} />
            <Route path="/AdminPanel" component={AdminPanel} />
            <Route path="/Teachers" component={Teachers} />
            <Route path="/Departments" component={Departments} />
            <Route path="/Results" component={Results} />
            <Route path="/About" component={About} />
            <Route path="/Studentlogin" component={StudentLogin} />
            <Route path="/Setting" component={Setting} />
            <Route component={NotFoundPage} />
          </Switch>
          <NotificationContainer />
        </Router>
      );
    }
    if (this.state.isauthuser) {
      rout = (
        <Router>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/Adminlogin" component={Adminlogin} />
            <Route path="/About" component={About} />
            <Route path="/Studentlogin" component={StudentLogin} />
            <Route path="/Studentpanel" component={StudentPanel} />
            <Route component={NotFoundPage} />
          </Switch>
          <NotificationContainer />
        </Router>
      );
    }
    return rout;
  }
}

ReactDOM.render(
  <HttpsRedirect>
    <Routes />
  </HttpsRedirect>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
