import React, { Component } from "react";
import Question from "../../Containers/User/Question";
import { MDBRow, MDBCard, MDBModalBody, MDBModal } from "mdbreact";
import Navbarr from "../../Containers/User/Navbarr";
import { NotificationManager } from "react-notifications";
import Auth2 from "../../HOC/Auth2";
import Axios from "axios";
import Select from "react-select";
export default class Studentpanel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      EVULATIONDONE: false,
      Questions: [],
      Courses: [],
      selectedCourse: "",
      courseteacher: "",
      isdone: false,
      userinfo: {
        UserDepartment: "",
        UserSemester: "",
        UserCourse: "",
        UserTeacher: "",
        UserBatch: ""
      }
    };
  }
  onCourseChange = e => {
    this.setState({
      ...this.state,
      userinfo: {
        ...this.state.userinfo,
        UserCourse: e.label,
        UserTeacher: e.teachername
      },
      selectedCourse: e
    });
  };
  onQuestionClick = (e, id) => {
    const i = this.state.Questions.findIndex(q => {
      return q.id === id;
    });
    const question = { ...this.state.Questions[i] };
    question.res = e.target.value;
    question.responce = parseInt(e.target.value, 10);
    const Questions = [...this.state.Questions];
    Questions[i] = question;
    let flag = true;
    this.state.Questions.map(e => {
      if (!e.res) {
        flag = false;
      }
    });
    this.setState({
      ...this.state,
      Questions: Questions
    });
  };
  getCoursesQuestions = async () => {
    await Axios.get("getstudentdata")
      .then(res => {
        if (res.data.data[0]) {
          let ques = res.data.Question.map(v => {
            return {
              res: null,
              responce: 0,
              id: v._id,
              ques: v.name,
              type: v.type
            };
          });
          let course = res.data.data.map(v => {
            return {
              teachername: v.teacherId,
              value: v.courseIdn,
              label: v.courseId
            };
          });
          this.setState({
            ...this.state,
            Courses: course,
            Questions: ques,
            selectedCourse: {
              value: course[0].value,
              label: course[0].label
            },
            userinfo: {
              ...this.state.userinfo,
              UserDepartment: res.data.data[0].departmentId,
              UserSemester: res.data.data[0].semesterId,
              UserBatch: res.data.data[0].batch,
              UserTeacher: course[0].teachername,
              UserCourse: course[0].label
            }
          });
        } else {
          this.evualtionisdone();
        }
      })
      .catch(err => {
        console.log(err);
      });
  };
  evualtionisdone = async () => {
    this.setState({
      EVULATIONDONE: true
    });
    NotificationManager.success(
      "Evulation Done loggin You Out",
      "Success!",
      1000
    );

    setTimeout(async () => {
      await Axios.get("usersignout")
        .then(res => {
          NotificationManager.success("Have A Nice Day", "Thank You!", 3000);
          this.props.history.push("/");
        })
        .catch(err => {
          console.log(err);
        });
    }, 3000);
  };
  onSubmit = e => {
    e.preventDefault();
    let res = true;
    this.state.Questions.map(e => {
      if (!e.res) {
        res = false;
      }
    });

    if (!res) {
      NotificationManager.error(
        "Kindly Attempt All The Answers",
        "Error!",
        3000
      );
    } else {
      let Question = this.state.Questions.map(e => {
        return {
          questionId: e.id,
          response: e.responce,
          type: e.type
        };
      });

      const data = {
        teacherId: this.state.userinfo.UserTeacher,
        courseId: this.state.selectedCourse.value,
        question: Question
      };

      Axios.post("result", {
        teacherId: this.state.userinfo.UserTeacher,
        courseId: this.state.selectedCourse.value,
        question: Question
      })
        .then(res => {
          NotificationManager.success("Evulation Submitted", "Success!", 3000);
          this.getCoursesQuestions();
        })
        .catch(err => {
          NotificationManager.error("Internal Server Error", "Error!", 3000);
          console.log(err);
        });
    }
  };

  componentDidMount() {
    this.getCoursesQuestions();
  }
  render() {
    let questionss = null;
    questionss = (
      <MDBRow className="justify-content-center">
        {this.state.Questions.map((question, i) => {
          return (
            <Question
              radio={question.res}
              id={question.id}
              onQuestion={e => this.onQuestionClick(e, question.id)}
              question={question.ques}
              type={question.type}
            />
          );
        })}
      </MDBRow>
    );
    console.log("state = ", this.state.Questions);
    return (
      <Auth2 {...this.props}>
        <Navbarr
          {...this.props}
          panal={true}
          data={this.state.userinfo}
          title="Teachers Evaluation"
        >
          {!this.state.EVULATIONDONE ? (
            <form onSubmit={this.onSubmit}>
              <div className="row  justify-content-center mt-5">
                <div className="col-2">
                  <label className="m-2">
                    <strong>Select Course</strong>
                  </label>
                </div>
                <div className="col-6">
                  <Select
                    label="Select department"
                    value={this.state.selectedCourse}
                    onChange={this.onCourseChange}
                    options={this.state.Courses}
                    required
                  />
                </div>
                <div className="col-2">
                  <button
                    className="btn btn-sm custombtn peach-gradient rounded-pill"
                    type="submit"
                  >
                    Submit
                  </button>
                </div>
              </div>

              {this.state.selectedCourse ? (
                <div className="mt-1">{questionss}</div>
              ) : null}
            </form>
          ) : (
            <div className="mt-5">
              <div className="d-flex m-auto justify-content-center">
                <h1>Your Evaluation Is Completed Thanks For Your Response</h1>
              </div>
              <div className="d-flex m-auto justify-content-center">
                <div className=" loader"></div>
              </div>
            </div>
          )}
        </Navbarr>
      </Auth2>
    );
  }
}
