import React, { PureComponent } from "react";
import Navbarr from "../../Containers/Admin/Navbarr";
import Card from "../../Containers/Admin/Card";
import Priv from "../../Extra/images/privacy.jpg";
import Res from "../../Extra/images/results.jpg";
import Ques from "../../Extra/images/question.jpg";
import Cous from "../../Extra/images/courses.jpg";
import { MDBRow, MDBContainer } from "mdbreact";
import Auth from "../../HOC/Auth";

const URL1 = "./Departments";
const URL2 = "./Teachers";
const URL3 = "./Courses";
const URL4 = "./Questions";
const URL5 = "./Results";
const URL6 = "./Tokens";
const card = {
  cards: [
    { id: "Departments", IMG: Cous, url: URL1 },
    { id: "Teachers", IMG: Cous, url: URL2 },
    { id: "Courses", IMG: Cous, url: URL3 },
    { id: "Questions", IMG: Ques, url: URL4 },
    { id: "Results", IMG: Res, url: URL5 },
    { id: "Tokens", IMG: Priv, url: URL6 }
  ]
};
export default class Home extends PureComponent {
  render() {
    let cards = null;
    cards = (
      <MDBRow className="justify-content-center mt-5">
        {card.cards.map(cards => {
          return <Card Source={cards.IMG} name={cards.id} urls={cards.url} />;
        })}
      </MDBRow>
    );

    return <Navbarr {...this.props}>{cards}</Navbarr>;
  }
}
