import React, { PureComponent } from "react";
import Btable from "../../Containers/Admin/BTable";
import Model from "../../Containers/Admin/ModeL";
import Navbarr from "../../Containers/Admin/Navbarr";
import { MDBModal, MDBContainer } from "mdbreact";
import { NotificationManager } from "react-notifications";
import Axios from "axios";
import Auth from "../../HOC/Auth";
import Loader from "../../Containers/Admin/Loader";
export default class Questions extends PureComponent {
  state = {
    isLoading: true,
    columns: [
      {
        label: "Question",
        field: "question",
        sort: "asc"
      },
      {
        label: "Type",
        field: "type",
        sort: "asc"
      },
      {
        label: "Delete",
        field: "delete",
        sort: "asc"
      }
    ],
    rows: [],
    modal: false
  };
  toggle = () => {
    this.setState({
      modal: !this.state.modal
    });
  };
  getQuestions = async () => {
    this.setState({ isLoading: true });
    await Axios.get("question")
      .then(res => {
        let rows = res.data.questions.map(v => {
          return {
            question: v.name,
            type: v.type,
            delete: (
              <button
                onClick={() => this.onDelete(v._id)}
                className="btn btn-danger btn-sm rounded-pill"
              >
                Delete
              </button>
            )
          };
        });
        this.setState({
          ...this.state,
          isLoading: false,
          rows
        });
      })
      .catch(err => {
        console.log(err);
        this.setState({
          isLoading: false
        });
      });
  };
  onDelete = async id => {
    this.setState({ isLoading: true });
    await Axios.post("del_question", { del_id: id })
      .then(res => {
        this.Refresh();
        NotificationManager.success(
          "Successfully deleted",
          "Successful!",
          3000
        );
      })
      .catch(err => {
        console.log(err);
        this.Refresh();
      });
  };
  async componentDidMount() {
    this.getQuestions();
  }
  Refresh = e => {
    this.setState({ modal: false });
    this.getQuestions();
  };
  render() {
    return (
      <Navbarr {...this.props}>
        <MDBContainer className="heavy-rain-gradient rounded hoverable mt-5 ">
          {this.state.isLoading ? (
            <Loader />
          ) : (
            <Btable data={this.state}>Questions</Btable>
          )}
        </MDBContainer>
        <MDBModal isOpen={this.state.modal} toggle={this.toggle}>
          <Model
            className="rounded hoverable animated zoomInUp"
            Type="question"
            scorllable
            responsive
            Refresh={this.Refresh}
            toggle={this.toggle}
          />
        </MDBModal>
        <MDBContainer className="text-center">
          <btn className=" btn btn-info rounded-pill" onClick={this.toggle}>
            New
          </btn>
        </MDBContainer>
      </Navbarr>
    );
  }
}
